.content { flex: 1 0 auto; }

.footer { flex: 0 0 auto; }

h1 { font-size: 32px; font-weight: 700; color: #FFF; }

h1.h1_item-page { color: #1e5995; text-align: center; font-size: 36px; margin-bottom: 12px; }

h2 { font-weight: 700; color: #333333; font-size: 36px; text-align: center; margin-bottom: 6px; }

h2.h1 { font-size: 36px; font-weight: 700; color: #1e5995; text-align: center; margin-bottom: 45px; }

h3 { font-weight: 700; color: #333333; font-size: 26px; line-height: 1.3; margin-bottom: 10px; }

.main-sub_title { color: #1e5995; font-size: 16px; font-family: 'Bodoni MT'; font-style: italic; font-weight: 400; text-align: center; margin-bottom: 50px; }

.main-btn { border-radius: 35px; background-color: #1e5995; color: #FFF; font-weight: 700; font-size: 18px; letter-spacing: 2px; text-transform: uppercase; display: inline-block; padding: 21px 46.5px; }

.main-btn:hover { background-color: #333333; }

.rate { width: 89px; height: 18px; margin: 5px 0; background: url("../img/rate-empty.png") repeat-x 0 0; }

.rate:before { display: block; content: " "; height: 18px; background: url("../img/rate-full.png") repeat-x 0 0; }

.rate-1:before { width: 18px; }

.rate-2:before { width: 37px; }

.rate-3:before { width: 55px; }

.rate-4:before { width: 73px; }

.rate-5:before { width: 89px; }

.tabs__caption { display: flex; flex-wrap: wrap; justify-content: space-between; padding: 0 80px; margin-bottom: 60px; }

.tabs__caption li { color: #939393; font-size: 24px; font-weight: 700; }

.tabs__caption li:not(.active) { cursor: pointer; }

.tabs__caption li:not(.active):hover { color: #1e5995; }

.tabs__caption .active { color: #1e5995; }

.tabs__content { display: none; }

.tabs__content p { font-size: 16px; line-height: 19px; margin-bottom: 40px; }

.tabs__content p:last-child { margin-bottom: 0; }

.tabs__content.active { display: block; }

.all-pages { padding: 100px 0; }

