input[type=number] { -moz-appearance: textfield; }

#customer_login { max-width: 400px; margin: 20px auto; }

#customer_login input { color: #1e5995; font-size: 16px; padding: 7px 20px; -webkit-border-radius: 18px; border-radius: 18px; width: 100%; border: 1px solid #1e5995; font-family: 'Open Sans', sans-serif; margin-bottom: 12px; }

#customer_login input.btn { color: #ffffff; font-size: 16px; font-family: 'Open Sans', sans-serif; background: #1e5995; border-radius: 16px; padding: 8px 46.5px; border: 0; transition: all 200ms; width: auto; display: block; }

#customer_login input.btn:hover { background: #333333; }

#customer_login .label { display: inline-block; margin-bottom: 10px; font-size: 16px; }

#customer_login a { font-size: 16px; font-weight: 600; }

#customer_login a:hover { color: #1e5995; }

#page-search form { margin: 20px 0; }

#page-search form input[type=text] { color: #1e5995; font-size: 16px; padding: 7px 20px; -webkit-border-radius: 18px; border-radius: 18px; border: 1px solid #1e5995; font-family: 'Open Sans', sans-serif; margin-right: 12px; }

#page-search form input[type=submit] { color: #ffffff; font-size: 16px; font-family: 'Open Sans', sans-serif; background: #1e5995; border-radius: 16px; padding: 8px 46.5px; border: 0; transition: all 200ms; width: auto; }

#page-search form input[type=submit]:hover { background: #333333; }

.search-results .result-image { margin-right: 20px; }

.search-results .result-image a { display: block; }

.search-results p { font-size: 16px; line-height: 1.3; }

.pagination { margin: 40px 0 20px; display: flex; flex-wrap: wrap; }

.pagination li { font-size: 16px; font-weight: 600; margin-right: 10px; }

