#page-cart .total #update-cart:hover { background: #1e5995; }

#page-cart .product-description { margin: 10px 0; }

#page-cart .price { font-size: 16px; font-weight: 700; }

#page-cart .cart { font-weight: 600; color: #333; }

.shopify-cart .prd, .shopify-cart .prc, .shopify-cart .rmv, .shopify-cart .qty { padding-bottom: 10px; }

.checkout-buttons { margin-bottom: 20px; }

.checkout-buttons label { display: inline-block; margin-bottom: 10px; font-size: 16px; }

.checkout-buttons textarea { width: 100%; resize: none; color: #1e5995; font-size: 16px; padding: 7px 20px; -webkit-border-radius: 18px; border-radius: 18px; border: 1px solid #1e5995; font-family: 'Open Sans', sans-serif; }

.header { padding: 8px 0; }

.header__container { display: flex; justify-content: space-between; align-items: center; }

.header__nav { display: flex; align-items: center; }

.header__nav ul { display: flex; margin-right: 30px; }

.header__nav ul li { margin-right: 18px; padding-right: 18px; position: relative; }

.header__nav ul li:before { content: ''; display: block; position: absolute; width: 4px; height: 4px; right: 0; top: 0; bottom: 0; margin: auto 0; background-color: #000; border-radius: 50%; }

.header__nav ul li:last-child { margin-right: 0; padding-right: 0; }

.header__nav ul li:last-child:before { display: none; }

.header__nav ul li a { font-size: 16px; }

.header__nav ul li a:hover { font-weight: 700; color: #1e5995; }

.header__nav ul .is-active a { font-weight: 700; color: #1e5995; }

.header__mobile-menu { width: 28px; height: 25px; border: 0; padding: 0; background-color: transparent; display: none; outline: none; transition: all 200ms ease; }

