.cart-link{
	margin: 20px 0;
}
.cart{
	position: fixed;
	right: 0;
	top: 0;
	width: 460px;
	height: 100vh;
	background-color: #fff;
	z-index: 100;
	transform: translateX(100%);
	transition: all 350ms ease;
	&.is-open{
		transform: translateX(0);
	}
}
.cart-head{
	background-color: #dfe7f4;
	height: 87px;
	display: flex;
	align-items: center;
	padding: 0 25px;
	&__back{
		background-image: url('../img/cart-back.png');
		background-size: 100%;
		background-repeat: no-repeat;
		width: 59px;
		height: 19px;
		padding: 0;
		background-color: transparent;
		border: 0;
		outline: none;
	}
	&__title{
		font-size: 24px;
		color: #1e5995;
		font-weight: 700;
		margin: 0 auto;
	}
}
.cart-list{
	padding-bottom: 80px;
	&__item{
		display: flex;
		padding: 30px 25px;
		border-bottom: 1px solid #c8c8c8;
	}
	&__image{
		border: 1px solid #c8c8c8;
		height: 80px;
		width: 80px;
		flex: none;
		margin-right: 25px;
		img{
			display: block;
			width: 100%;
			height: 100%;
			padding: 5px;
		}
	}
	&__content{
		background-color: #fff;
		display: flex;
		flex-direction: column;
	}
	&__bottom{
		display: flex;
		justify-content: space-between;
		margin-top: auto;
	}
	&__title{
		font-weight: 600;
		font-size: 19px;
		color: #000000;
		line-height: 1.2;
	}
	&__price{
		font-size: 14px;
		color: #8e8e8e;
		font-weight: 700;
	}
}
.cart-content{
	overflow: auto;
	height: calc(100% - 87px);
}
.cart-plus-minus{
	display: flex;
	align-items: center;
	&__count{
		font-family: 'Open Sans', sans-serif;
		border: 3px solid #c8c8c8;
		font-size: 13px;
		height: 25px;
		width: 25px;
		text-align: center;
		color: #8e8e8e;
		font-weight: 700;
		padding: 0;
	}
	&__btns{
		display: flex;
		flex-direction: column;
		margin-right: 5px;
		position: relative;
		top: 2px;
	}
	&__count-btn{
		border: 0;
		background-color: transparent;
		padding: 0;
		width: 12px;
		height: 12px;
		background-repeat: no-repeat;
		background-size: cover;
	}
	&__count-btn--plus{
		background-image: url('../img/svg/plus-black-symbol.svg');
	}
	&__count-btn--minus{
		background-image: url('../img/svg/minus-symbol.svg');
	}
}
.cart-sub-total{
	background-color: #1e5894;
	padding: 0 25px;
	height: 60px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: auto;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	&__text{
		font-weight: 700;
		font-size: 18px;
		color: #FFF;
		text-transform: uppercase;
		position: relative;
		padding-left: 25px;
		&:before {
			content: '';
			display: block;
			position: absolute;
			width: 17px;
			height: 19px;
			left: 0;
			top: 0;
			background-image: url('../img/svg/padlock.svg');
			background-repeat: no-repeat;
			background-size: cover;
		}
	}
	&__price{
		color: #FFF;
		font-size: 16px;
		font-weight: 600;
	}
}
@include r(480){
	.cart{
		width: 100%;
	}
}