.header{
	padding: 8px 0;
	&__container{
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__nav{
		display: flex;
		align-items: center;
		ul{
			display: flex;
			margin-right: 30px;
			li{
				margin-right: 18px;
				padding-right: 18px;
				position: relative;
				&:before {
					content: '';
					display: block;
					position: absolute;
					width: 4px;
					height: 4px;
					right: 0;
					top: 0;
					bottom: 0;
					margin: auto 0;
					background-color: #000;
					border-radius: 50%;
				}
				&:last-child{
					margin-right: 0;
					padding-right: 0;
					&:before{
						display: none;
					}
				}
				a{
					font-size: 16px;
					&:hover{
						font-weight: 700;
						color: #1e5995;
					}
				}
			}
			.is-active{
				a{
					font-weight: 700;
					color: #1e5995;
				}
			}
		}
	}
	&__mobile-menu{
		width: 28px;
		height: 25px;
		border: 0;
		padding: 0;
		background-color: transparent;
		display: none;
		outline: none;
		transition: all 200ms ease;
		span{
			display: block;
			height: 3px;
			margin-bottom: 4px;
			background-color: #333333;
			transition: all 200ms ease;
			&:last-child{
				margin-bottom: 0;
			}
		}
		&.is-active{
			span{
				&:first-child{
					transform: rotate(135deg);
				}
				&:nth-child(2){
					display: none;
				}
				&:nth-child(3){
					transform: rotate(-135deg);
					margin-top: -7px;
				}
			}
		}
	}
}
.head-basket{
	position: relative;
	&__icon{
		.svg-icon{
			width: 20px;
			height: 15px;
		}
	}
	&__value{
		position: absolute;
		top: -6px;
		right: -6px;
		min-width: 12px;
		height: 12px;
		background-color: #1e5897;
		border-radius: 50%;
		font-size: 8px;
		font-weight: 700;
		color: #FFF;
		text-align: center;
		line-height: 12px;
	}
}
.main-banner{
	&__container{
		padding: 50px 20px 20px;
		background-image: url('../img/main-banner/main-banner-1.jpg');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		margin: 0 40px;
	}
	&__sub-title{
		font-weight: 700;
		color: #FFF;
		font-size: 14px;
		margin-bottom: 2px;
	}
	&__title{
		font-size: 36px;
		color: #FFF;
		font-weight: 700;
		margin-bottom: 37px;
	}
	p{
    	font-family:'Bodoni MT';
		font-style: italic;
		font-weight: 400;
		font-size: 16px;
		color: #FFF;
		line-height: 19px;
		margin-bottom: 38px;
		max-width: 500px;
	}
	&__btn{
		a{
			background-color: #1e5995;
			color: #FFF;
			font-size: 20px;
			font-weight: 700;
			padding: 10.5px 47px;
			display: inline-block;
			&:hover{
				background-color: #333333;
			}
		}
	}
}
.trending{
	padding: 40px 0 14px;
}
.trending-list{
	display: flex;
	flex-wrap: wrap;
	margin: 0 -6px;
	&__item{
		width: calc(25% - 12px);
		margin: 0 6px;
		margin-bottom: 40px;
	}
	&__content{
		text-align: center;
	}
	&__title{
		color: #333333;
		font-weight: 700;
		font-size: 20px;
		margin-bottom: 8px;
	}
	&__image{
		height: 260px;
		margin-bottom: 18px;	
		img{
			display: block;
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}
	&__price{
		color: #808080;
		font-size: 11.4px;
	}
}
.video-section{
	background-image: url('../img/video-section-bg.jpg');
	background-repeat: no-repeat;
	background-position: center;
	min-height: 420px;
	&__container{
		padding: 86px 0 0 70px;
		max-width: 400px;
	}
	&__title{
		font-size: 36px;
		color: #ffffff;
		font-weight: 700;
		line-height: 40px;
		margin-bottom: 15px;
	}
	p{
		font-family:'Bodoni MT';
		font-style: italic;
		font-weight: 400;
		font-size: 16px;
		color: #FFF;
		line-height: 19px;
		margin-bottom: 48px;
	}
}
.our-product{
	padding-top: 20px;
	padding-bottom: 73px;
	&__btn{
		text-align: center;
		margin-bottom: 92px;	
	}
}
.product-info{
	display: flex;
	flex-wrap: wrap;
	&__item{
		width: calc(50% - 24px);
		margin: 0 12px;
		min-height: 180px;
		p{
			color: #72622f;
			font-family:'Bodoni MT';
			font-style: italic;
			font-weight: 700;
			font-size: 17px;
			line-height: 19px;
			border: 2px solid #72622f;
			padding: 20px 80px;
			text-align: center;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		span{
			display: flex;
			justify-content: center;
			align-items: center;
			color: #72622f;
			text-transform: uppercase;
			letter-spacing: 3px;
			font-size: 18px;
			font-weight: 700;
			height: 100%;
		}
	}
	&__item--bg{
		background-color: #efd160;
	}
}
.stories{
	padding-bottom: 114px;
	.main-sub_title{
		margin-bottom: 23px;
	}
	&__btn{
		text-align: center;
	}
}
.stories-list{
	display: flex;
	flex-wrap: wrap;
	margin: 0 -6px 47px;
	&__item{
		width: 25%;
		padding: 0 6px;
	}
	&__image{
		height: 261px;
		margin-bottom: 12px;
		img{
			display: block;
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}
	&__title{
		color: #333333;
		font-size: 18px;
		font-weight: 600;
		line-height: 21px;
		margin-bottom: 9px;
	}
	&__date{
		font-size: 11px;
		color: #808080;
		font-weight: 600;
	}
}
