	&__title{
		font-size: 24px;
		color: #ffffff;
		font-weight: 700;
		text-align: center;
		margin-bottom: 10px;
	}
	&__item{
		width: 33.3333%;
		padding: 0 30px;
		p{
			text-align: center;
			font-size: 18px;
			color: #FFF;
			line-height: 21px;
		}
	}
	&__payments{
		padding-left: 60px;
	}
	&__promise{
		position: relative;
		&:before {
			content: '';
