.footer-bottom{
	padding: 50px 0 45px;
	&__container{
		display: flex;
		flex-wrap: wrap;
		margin: 0 -20px;
	}
	&__item{
		width: 33.3333%;
		padding: 0 20px;
	}
	&__title{
		font-size: 24px;
		color: #1e5995;
		font-weight: 700;
		margin-bottom: 25px;
	}
	p{
		font-size: 16px;
