		padding: 7px 20px;
		border-radius: 18px;
		width: 100%;
		border: 1px solid #1e5995;
		font-family: 'Open Sans', sans-serif;
		&::-webkit-input-placeholder {color: #1e5995;}
		&::-moz-placeholder          {color: #1e5995;}
		&:-moz-placeholder           {color: #1e5995;}
		&:-ms-input-placeholder      {color: #1e5995;}
	}
