		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 241px;
		left: 0;
		top: 0;
		background-image: url('../img/category-bg.jpg');
		background-position: top center;
		background-repeat: no-repeat;
		background-size: cover;
	}
}
.category_in{
	position: relative;
	z-index: 1;
	&__top{
		background-color: #1e5995;
		padding: 15px 40px;
	}
	&__content{
		background-color: #fff;
		padding: 35px;
		box-shadow: 0 2px 2px 0 rgba(0,0,0,.2);
	}
}
.category-list{
	display: flex;
	flex-wrap: wrap;
	margin: 0 -11px;
	&__item{
		width: 50%;
		padding: 0 11px;
		margin-bottom: 30px;
		&:nth-child(n+3){
			margin-bottom: 0;
		}
	}
	&__image{
		img{
			display: block;
			width: 100%;
		}
	}
	&__content{
		padding: 40px 20px;
		border-left: 1px solid #a3bacc;
		border-right: 1px solid #a3bacc;
		border-bottom: 1px solid #a3bacc;
		text-align: center;
	}
	&__title{
		color: #000000;
		font-size: 18px;
		font-weight: 700;
		margin-bottom: 25px;
	}
	p{
		font-size: 12px;
		line-height: 15px;
		margin-bottom: 25px;
	}
	&__btn{
		a{
			font-size: 16px;
			color: #FFF;
			display: inline-block;
			padding: 10px 29px;
			background-color: #1e5995;
			&:hover{
				background-color: #333333;
			}
		}
	}
}
.bread-crumbs{
	margin-bottom: 10px;
	ul{
		display: flex;
		flex-wrap: wrap;
		li{
			color: #FFF;
			font-size: 14px;
			margin-right: 50px;
			&:last-child{
				margin-right: 0;
			}
			a{
				color: #FFF;
				font-size: 14px;
			}
		}
	}
}
.product-item{
	&__container{
		display: flex;
		margin-bottom: 100px;
	}
	&__images{
		width: 404px;
		margin-right: 65px;
	}
