		}
	}
}
.product-item{
	margin-top: 80px;
	&__sub-title{
		text-align: center;
		font-size: 16px;
		color: #939393;
		margin-bottom: 50px;
	}
	&__reviews{
		display: flex;
		align-items: center;
		margin-bottom: 30px;
		span{
			display: block;
			margin-left: 10px;
			color: #b2b2b2;
			font-size: 14px;
		}
	}
	&__price{
		font-size: 24px;
		color: #939393;
		margin-bottom: 30px;
	}
}
.item-images{
	margin-bottom: 7px;
	&__zoom{
		position: absolute;
		top: 20px;
		right: 20px;
		a{
			background: url('../img/item-zoom.png') 0 0 no-repeat;
			width: 41px;
			height: 41px;
			display: block;
			z-index: 2;
		}
	}
	&__item{
		outline: none;
		position: relative;
	}
	&__image{
		img{
			width: 100%;
		}
	}
}
.item-images-thumbs{
	&__item{
		outline: none;
		opacity: .5;
		&.slick-current{
			opacity: 1;
		}
	}
	&__image{
		margin: 0 auto;
		width: 95px;
		img{
			width: 100%;
		}
	}
}
.item-social{
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 165px;
	max-width: 320px;
	&__save{
		a{
			display: flex;
			align-items: center;
		}
		.svg-icon{
			width: 19px;
			height: 16px;
			fill: #939393;
			display: block;
			margin-right: 5px;
		}
		span{
			display: block;
			font-size: 14px;
			color: #939393;
		}
	}
	&__list{
		display: flex;
		align-items: center;
		ul{
			display: flex;
			align-items: center;
			li{
				margin-right: 7px;
				&:last-child{
					margin-right: 0;
				}
				a{
					.svg-icon{
						width: 23px;
						height: 23px;
						fill: #939393;
						display: block;
					}
					.svg-icon--google{
						width: 35px;
					}
				}
			}
		}
		span{
			display: block;
			font-size: 14px;
			color: #939393;
			margin-right: 10px;
		}
	}
}