@include r(1023){
	.header{
		&__nav {
			ul{
				li{
					padding-right: 12px;
					margin-right: 12px;
					a{
						font-size: 14px;
					}
				}
			}
		}
	}
	.stories-list{
		&__item{
			width: 50%;
		}
	}
	.footer-top{
		&__payments{
			padding-left: 30px;
		}
	}
	.category{
		padding-top: 50px;
	}
	.category-list{
		&__content{
			padding: 20px;
		}
	}
	.product-item{
		margin-top: 20px;
		&__images{
			width: 350px;
			margin-right: 20px;
		}
	}
	.item-social{
		max-width: 100%;
		margin-top: 100px;
	}
	.tabs__caption{
		padding: 0;
	}
}
@include r(767){
	.header{
		&__mobile-menu{
			display: block;
		}
		&__container{
			position: relative;
		}
		&__nav{
			position: absolute;
			top: 100%;
			left: 0;
			margin-left: -20px;
			background-color: #fff;
			padding: 30px;
			transform: translateX(-100%);
			transition: all 200ms ease;
			z-index: 50;
			&.is-open{
				transform: translateX(0);
			}
			ul{
				flex-direction: column;
				margin-right: 0;
				li{
					padding-right: 0;
					margin: 0 0 15px;
					&:last-child{
						margin: 0;
					}
					&:before{
						display: none;
					}
					a{
						font-size: 16px;
					}
				}
			}
		}
	}
	.main-banner{
		&__container{
			margin: 0;
		}
	}
	.trending-list{
		&__item{
			width: calc(50% - 12px);
		}
	}
	.video-section{
		background-position: right;
		&__container{
			max-width: 100%;
			padding: 20px 0 0 0;
		}
	}
	.product-info{
		&__info{
			flex-direction: column;
		}
		&__item{
			width: 100%;
			margin: 0 0 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	.stories{
		padding-bottom: 50px;
	}
	.footer-bottom{
		&__container{
			flex-direction: column;
		}
		&__item{
			width: 70%;
			margin: 0 auto 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	.footer-top{
		&__container{
			flex-direction: column;
		}
		&__item{
			width: 100%;
			margin: 0 auto 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
		&__payments{
			margin-bottom: 60px;
		}
		&__payments_all{
			text-align: center;
		}
		&__payments_image{
			img{
				margin: auto;
			}
		}
	}
	.category_in{
		&__content{
			padding: 20px;
		}
	}
	h1{
		font-size: 27px;
	}
	h1.h1_item-page{
		font-size: 27px;
	}
	h2.h1{
		font-size: 27px;
		margin-bottom: 25px;
	}
	.bread-crumbs{
		 ul {
		 	li {
		 		margin-right: 20px;
		 		a{
		 			font-size: 13px;
		 		}
		 	}
		 }
	}
	.product-item{
		&__container{
			flex-direction: column;
			margin-bottom: 50px;
		}
		&__images{
			margin: 0 0 30px;
			width: 100%;
		}
	}
	.tabs__caption {
		margin-bottom: 30px;
		li{
			font-size: 15px;
		}
	}
	.tabs__content {
		p{
			margin-bottom: 20px;
			font-size: 14px;
		}
	}
}
@include r(480){
	.trending-list{
		&__item{
			width: 100%;
		}
	}
	.product-info{
		
		&__item{
			p{
				padding: 20px;
			}
		}
	}
	.stories-list{
		&__item{
			width: 100%;
			margin-bottom: 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	.footer-bottom{
		&__item{
			width: 100%;
		}
	}
	.category-list{
		&__item{
			width: 100%;
			margin-bottom: 20px;
			&:nth-child(n+3){
				margin-bottom: 20px;
			}
		}
	}
	.product-item{
		&__plus-minus{
			width: 120px;
			height: 50px;
		}
		&__add-to-bag{
			a{
				padding: 14px 16px;
				font-size: 14px;
			}
		}
		&__price{
			margin-bottom: 20px;
		}
		&__reviews{
			margin-bottom: 20px;
		}
		&__sub-title{
			margin-bottom: 30px;
		}
	}
	.item-social{
		margin-top: 40px;
		flex-direction: column;
		align-items: flex-start;
		&__save{
			margin-bottom: 20px;
		}
	}
	.tabs__caption {
		flex-direction: column;
		li{
			margin-bottom: 15px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}