ul li{
	list-style: none;
}
img{
	max-height: 100%;
}
body {
  	background: #FFFFFF;
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
	font-size: 14px;
}
// body.pixelperfect{
//     background: url('../img/pp-1.jpg') center 0 no-repeat;
//     opacity: 0.7;
// }
.out {
	min-width: 320px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}
.wrapper {
	max-width: 988px;
	margin: auto;
	padding: 0 20px;
}
.wrapper-large{
    max-width: 1028px;
    margin: auto;
    padding: 0 20px;
}
.content {
	flex: 1 0 auto;
}
.footer{
    flex: 0 0 auto;
}
h1{
    font-size: 32px;
    font-weight: 700;
    color: #FFF;
}
h1.h1_item-page{
    color: #1e5995;
    text-align: center;
    font-size: 36px;
    margin-bottom: 12px;
}
h2{
    font-weight: 700;
    color: #333333;
    font-size: 36px;
    text-align: center;
    margin-bottom: 6px;
}
h2.h1{
    font-size: 36px;
    font-weight: 700;
    color: #1e5995;
    text-align: center;
    margin-bottom: 45px;
}
h3{
    font-weight: 700;
    color: #333333;
    font-size: 26px;
    line-height: 1.3;
    margin-bottom: 10px;
}
.main-sub_title{
    color: #1e5995;
    font-size: 16px;
    font-family:'Bodoni MT';
    font-style: italic;
    font-weight: 400;
    text-align: center;
    margin-bottom: 50px;
}
.main-btn{
    border-radius: 35px;
    background-color: #1e5995;
    color: #FFF;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 2px;
    text-transform: uppercase;
    display: inline-block;
    padding: 21px 46.5px;
    &:hover{
        background-color: #333333;
    }
}
.rate {
  width: 89px;
  height: 18px;
  margin: 5px 0;
  background: url('../img/rate-empty.png') repeat-x 0 0;
}
.rate:before {
  display: block;
  content: " ";
  height: 18px;
  background: url('../img/rate-full.png') repeat-x 0 0;
}
.rate-1:before {
  width: 18px;
}
.rate-2:before {
  width: 37px;
}
.rate-3:before {
  width: 55px;
}
.rate-4:before {
  width: 73px;
}
.rate-5:before {
  width: 89px;
}
.tabs__caption {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 80px;
    margin-bottom: 60px;
    li{
        color: #939393;
        font-size: 24px;
        font-weight: 700;
        &:not(.active){
            cursor: pointer;
        }
        &:not(.active):hover{
            color: #1e5995;
        }
    }
    .active{
        color: #1e5995;
    }
}
.tabs__content {
    display: none;
    p{
        font-size: 16px;
        line-height: 19px;
        margin-bottom: 40px;
        &:last-child{
            margin-bottom: 0;
        }
    }
    &.active{
        display: block;
    }
}
.all-pages{
    padding: 100px 0;
    ol{
        li{
            margin-bottom: 20px;
            a{
                &:visited{
                    color: #8709cf;
                }
            }
        }
    }
}
input[type=number] {
    -moz-appearance:textfield;
}
#customer_login{
    max-width: 400px;
    margin: 20px auto;
    input{
        color: #1e5995;
        font-size: 16px;
        padding: 7px 20px;
        -webkit-border-radius: 18px;
        border-radius: 18px;
        width: 100%;
        border: 1px solid #1e5995;
        font-family: 'Open Sans', sans-serif;
        margin-bottom: 12px;
    }
    input.btn{
        color: #ffffff;
        font-size: 16px;
        font-family: 'Open Sans', sans-serif;
        background: #1e5995;
        border-radius: 16px;
        padding: 8px 46.5px;
        border: 0;
        transition: all 200ms;
        width: auto;
        display: block;
        &:hover{
            background: #333333;
        }
    }
    .label{
        display: inline-block;
        margin-bottom: 10px;
        font-size: 16px;
    }
    a{
        font-size: 16px;
        font-weight: 600;
        &:hover{
            color: #1e5995;
        }
    }
}
#page-search{
    form{
        margin: 20px 0;
        input[type=text]{
            color: #1e5995;
            font-size: 16px;
            padding: 7px 20px;
            -webkit-border-radius: 18px;
            border-radius: 18px;
            border: 1px solid #1e5995;
            font-family: 'Open Sans', sans-serif;
            margin-right: 12px;
        }
        input[type=submit]{
            color: #ffffff;
            font-size: 16px;
            font-family: 'Open Sans', sans-serif;
            background: #1e5995;
            border-radius: 16px;
            padding: 8px 46.5px;
            border: 0;
            transition: all 200ms;
            width: auto;
            &:hover{
                background: #333333;
            }
        }
    }
}
.search-results{
    .result-image{
        margin-right: 20px;
        a{
            display: block;
        }
    }
    p{
        font-size: 16px;
        line-height: 1.3;
    }
}
.pagination{
    margin: 40px 0 20px;
    display: flex;
    flex-wrap: wrap;
    li{
        font-size: 16px;
        font-weight: 600;
        margin-right: 10px;
        &:first-child{
            margin-right: 0;
        }
        a{
            font-size: 16px;
            font-weight: 600;
            color: #1e5995;
            &:hover{
                color: #333;
            }
        }
    }
}
#page-cart{
    table{
        margin-bottom: 30px;
        input[type=text]{
            border: 1px solid #4778a9;
            font-family: 'Open Sans', sans-serif;
            font-size: 16px;
            padding: 10px;
            text-align: center;
        }
    }
    .total{
        p{
            margin-bottom: 20px;
            font-size: 16px;
            font-weight: 700;
        }
        input[type=submit]{
            color: #ffffff;
            font-size: 16px;
            font-family: 'Open Sans', sans-serif;
            background: #1e5995;
            border-radius: 16px;
            padding: 8px 46.5px;
            border: 0;
            transition: all 200ms;
            width: auto;
            &:hover{
                background: #333333;
            }
        }
        #update-cart{
            background: #333333;
            &:hover{
                background: #1e5995;
            }
        }
    }
    .product-description{
        margin: 10px 0;
    }
    .price{
        font-size: 16px;
        font-weight: 700;
    }
    .cart{
        font-weight: 600;
        color: #333;
    }
}
.shopify-cart .prd, .shopify-cart .prc, .shopify-cart .rmv, .shopify-cart .qty{
    padding-bottom: 10px;
}
.checkout-buttons{
    margin-bottom: 20px;
    label{
        display: inline-block;
        margin-bottom: 10px;
        font-size: 16px;
    }
    textarea{
        width: 100%;
        resize: none;
        color: #1e5995;
        font-size: 16px;
        padding: 7px 20px;
        -webkit-border-radius: 18px;
        border-radius: 18px;
        border: 1px solid #1e5995;
        font-family: 'Open Sans', sans-serif;
    }
}